.stopwatch {
  background-color: #fff;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up .stopwatch__block:first-child {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__block:nth-child(2) {
  animation-duration: 10s;
}

.minutes-hand {
  width: 3vmin;
  height: 40vmin;
  margin: auto;
  animation-name: minutes-hand-animation;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  inset: 0;
}

.minutes-hand:before {
  content: "";
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: auto;
  position: absolute;
  inset: -20vmin 0 0;
}

@keyframes minutes-hand-animation {
  100% {
    transform: rotate(360deg);
  }
}

.seconds-hand {
  width: 1.5vmin;
  height: 76vmin;
  margin: auto;
  animation-name: seconds-hand-animation;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  inset: 0;
}

.seconds-hand:before {
  content: "";
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin: auto;
  position: absolute;
  inset: -38vmin 0 0;
}

@keyframes seconds-hand-animation {
  100% {
    transform: rotate(360deg);
  }
}

.center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

body {
  margin: 0;
}

.page-content {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}
